import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import download from "downloadjs";

import Template1 from "assets/card-template-1.png";
import Template2 from "assets/card-template-2.png";

import "./style.scss";

export default function Home() {
  const [cardUrl, setCardUrl] = useState("");

  const handleDownload = () => {
    download(localStorage.getItem("dataUrl"), "card.png");
  };

  useEffect(() => {
    const imageStorage = localStorage.getItem("dataUrl");
    if (imageStorage) {
      setCardUrl(imageStorage);
    }
  }, []);

  return (
    <>
      <div className="mb-4">
        <h2 className="h2">Your Card Design</h2>
        {cardUrl === "" ? (
          <p>You don't have any card design. Create one from below template</p>
        ) : (
          <div className="card-result position-relative">
            <img src={cardUrl} width={300} alt="your-design" />
            <div className="overlay d-flex justify-content-center align-items-center">
              <button className="btn btn-secondary" onClick={handleDownload}>
                Download
              </button>
            </div>
          </div>
        )}
      </div>
      <div>
        <h2 className="h2">Available Template</h2>
        <div
          className="d-flex justify-content-between"
          style={{ width: "50%" }}
        >
          <Link
            to={{
              pathname: "/form",
              state: {
                templateId: "card1",
              },
            }}
            className="template-card"
          >
            <img
              src={Template1}
              width={300}
              className="mr-3"
              alt="template-1"
            />
          </Link>
          <Link
            to={{
              pathname: "/form",
              state: {
                templateId: "card2",
              },
            }}
            className="template-card"
          >
            <img src={Template2} width={300} alt="template-2" />
          </Link>
        </div>
      </div>
    </>
  );
}
