import React, { useRef } from "react";
import { useLocation, useHistory } from "react-router-dom";
import { toPng } from "html-to-image";

import { Card1, Card2 } from "./cards";

import "./style.scss";

const CARDS = {
  card1: (payload) => <Card1 payload={payload} />,
  card2: (payload) => <Card2 payload={payload} />,
};

export default function CardName() {
  const ref = useRef(null);
  const history = useHistory();
  const { state = {} } = useLocation();
  const { templateId = null, payload = {} } = state;

  if (!templateId) {
    history.push("/");
  }

  const handleSubmit = async () => {
    try {
      const dataUrl = await toPng(ref.current);
      localStorage.setItem("dataUrl", dataUrl);
      history.replace("/");
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <div className="d-flex flex-column align-items-center">
      <div className="card card-name max-auto" ref={ref}>
        {CARDS[templateId](payload)}
      </div>
      <button
        type="button"
        className="btn btn-primary my-4"
        onClick={handleSubmit}
      >
        Save
      </button>
    </div>
  );
}
